




















































import { Component, Vue } from "vue-property-decorator";
import HelloWorld from "@/components/HelloWorld.vue"; // @ is an alias to /src
import { loadScript } from "@/services/utilService";

@Component({
  components: {
    HelloWorld,
  },
  mounted() {
    loadScript(
      "https://imaginemos-ae.github.io/com.emprender.FrontEnd-demo/credit-simulator/dist/emprender-credit-simulator/emprender-credit-simulator.esm.js",
      "credit-simulator",
      "module"
    );
    const selector = "emprender-credit-simulator";
    customElements
      .whenDefined(selector)
      .then((this as any).addRequestListener());
  },
  methods: {
    addRequestListener() {
      (this.$refs.simulator as any)?.addEventListener(
        "creditRequested",
        (event: any) => {
          console.log("Credit requested", event.detail);
          this.$router.push("user-biometrics");
        }
      );
    },
  },
})
export default class Simulator extends Vue {}
