





































































import { Component, Vue } from "vue-property-decorator";
import HelloWorld from "@/components/HelloWorld.vue"; // @ is an alias to /src
import { loadScript } from "@/services/utilService";

@Component({
  components: {
    HelloWorld,
  },
  mounted() {
    loadScript(
      "https://imaginemos-ae.github.io/com.emprender.FrontEnd-demo/user-biometrics/dist/emprender-user-biometrics/emprender-user-biometrics.esm.js",
      "user-biometrics",
      "module"
    );
    const selector = "emprender-user-biometrics";
    customElements
      .whenDefined(selector)
      .then((this as any).addRequestListener());
  },
  methods: {
    addRequestListener() {
      (this.$refs.simulator as any)?.addEventListener(
        "creditRequested",
        (event: any) => {
          console.log("Credit requested", event.detail);
          this.$router.push("user-biometrics");
        }
      );
    },
    greet: function (event) {
      const adoURL = "https://adocolombia-qa.ado-tech.com/EmprenderQA/validar-persona?callback=http%3A%2F%2Fcredito.muii.com.co%2Fcredito%2F%23%2Fuser-biometrics-success&key=db92efc69991&projectName=EmprenderQA&product=1";
      window.location.assign(adoURL); 
    },
  },
})
export default class Biometrics extends Vue {}
